.place{
    padding: 4rem 7%;
    font-family: 'Red Rose', cursive;
    flex-direction: column;
    display: flex;
}
.crdssec{
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    gap: 1.5rem;
}
.plcnav{
    padding: 2rem 0 4rem 0;
    font-size: 1.3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: #434343;
    width: 100%;
}
.fltrtxt{
    font-size: 1rem;
}
.filterbtn{
    border: 1px solid #B4B4B4;
    border-radius: 8px;
    padding: .7rem 1.2rem;
    width: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: pointer;
}
.flt{
    width: 18%;
}

@media only screen and (max-width: 943px){
    .plcnav{
        font-size: 1.1rem;
        padding: 2rem 0;
    }
    .filterbtn{
        padding: .7rem 1.2rem;
    }
    .fltrtxt{
        font-size: .8rem;
    }
    .place{
        padding: 3rem 7%;
        display: flex;
        align-items: center;
    }
}
@media only screen and (max-width: 768px){
    .plcnav{
        padding: 0 0 1.5rem 0;
        font-size: 1rem;
        width: 100%;
    }
    .crdssec{
        width: 90%;
        display: grid;
        grid-template-columns: auto auto  ;
        grid-template-rows: auto auto auto auto;
        gap: 1.5rem;
    }
    .rem{
        display: none;
    }
    .filterbtn{
        width: 13%;
    }
    .place{
        flex-direction: column;
        display: flex;
    }
}
@media only screen and (max-width: 568px){
    .rem1{
        display: none;
    }
    .plcnav{
        width: 90%;
    }
}
@media only screen and (max-width: 376px){
    .plcnav{
        font-size: .9rem;
        width: 100%;
    }
    .navg{
        display: none;
    }
    .filterbtn{
        padding: .7rem;
        width: 30%;
    }
    .crdssec{
        display: grid;
        grid-template-columns: auto   ;
        grid-template-rows: auto auto auto auto;
    }
}
