.Navbar{
    background-color: transparent;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    height: 3.5rem;
    padding: 0 7%;
    box-sizing: border-box;
}
.logoprt, .lnkprt, .btnprt{
    display: flex;
    flex-direction: row;
}
.logoprt{
    width: 10%;
    align-items: baseline;
    gap: 2%;
}
.lnkprt{
    width: 35%;
    justify-content: space-between;
}
.logo{
    width: 35%;
}
.logotxt{
    background-image: linear-gradient(91.47deg, #DC3E88 18.25%, #B840AE 43.34%, #843FE8 78.18%);
    background-clip: text;
    color: transparent;
    font-size: 2.7rem;
    font-weight: 650;
}
.link{
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
}
.link, .btnprt{
    font-family: 'Red Rose', cursive;
}
.btnprt{
    width: 13%;
    height: 90%;
    background-image: linear-gradient(90deg, #A02279 11.45%, #A02279 11.45%);
    color: white;
    font-size: .9rem;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;
    text-decoration: none;
}
.btnprt:hover{
    background-image: linear-gradient(90deg, #771c5a 11.45%, #771c5a 11.46%);
}
.Overlay{
    position: fixed;
    display: none;
    place-items: center;
    grid-template-columns: 1/2;
    grid-template-rows: 1/2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 2;
    font-family: 'Red Rose', cursive;

}
.Ovrlycrd{
    background-color: white;
    width: 40%;
    height: 20rem;
    border-radius: 16px;
}
.ovrlyhdr{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    padding: 1.3rem 1.3rem 0 1.3rem;
    box-sizing: border-box;
    height: 15%;
}
.ovrlyhd{
    margin: 0;
    font-size: 1.5rem;
}
.close{
    width: 5%;
    cursor: pointer;
}
hr{
    margin: 1.5rem 0;
}
.ovrlycntnt{
    box-sizing: border-box;
    padding: 0 1.3rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.ovrlybtn{
    border: 1px solid #CFD8DC;
    border-radius: 12px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    padding: .6rem 1.1rem;
    cursor: pointer;
}
.cntnthdC{
    font-size: 1rem;
}
.ovrlybtn:hover{
    background-color: #F8F9FA;;
}
.inbtn{
    width: 40%;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    align-items: center;
}
.obtntxt{
    font-size: 1.15rem;
    font-weight: 600;
}
.btnimg{
    width: 25%;
}
.menu{
    display: none;
}
.sidemnu{
    display: none;
}

/* media queries */
@media only screen and (max-width: 1173px){
    .logotxt{
        font-size: 2.5rem;
    }
    .lnkprt{
        width: 40%;
    }
    .btnprt{
        width: 15%;
    }
}

@media only screen and (max-width: 1073px){
    .logoprt{
        width: 15%;
        align-items: baseline;
        gap: 2%;
    }
    .lnkprt{
        width: 42%;
    }
    .logotxt{
        font-size: 2.3rem;
    }
    .Navbar{
        padding: 0 6%;
    }
}
@media only screen and (max-width: 943px){
    .link{
        font-size: 1.1rem;
    }
    .btnprt{
        width: 16%;
        height: 90%;
        font-size: .8rem;
    }
    .logo{
        width: 30%;
    }
    .Ovrlycrd{
        height: 18rem;
    }
}
@media only screen and (max-width: 843px){
    .logotxt{
        font-size: 2rem;
    }
    .lnkprt{
        width: 49%;
    }
    .btnprt{
        width: 18%;
        height: 90%;
        font-size: .8rem;
    }
    .Navbar{
        height: 3rem;
    }
    .logoprt{
        width: 20%;
    }
    .logo{
        width: 23%;
    }
    .link{
        font-size: 1rem;
    }
    .Ovrlycrd{
        height: 17rem;
    }
}
@media only screen and (max-width: 768px){
    .menu{
        width: 4%;
    }
    .logotxt{
        font-size: 1.5rem;
    }
    .link{
        font-size: .9rem;
    }
    .btnprt{
        width: 17%;
        height: 90%;
        font-size: .7rem;
    }
    .Ovrlycrd{
        width: 50%;
    }
    .obtntxt{
        font-size: 1rem;
    }
    .ovrlyhd{
        font-size: 1.2rem;
    }
    .cntnthdC{
        font-size: .9rem;
    }
}
@media only screen and (max-width: 668px){
    .Ovrlycrd{
        width: 60%;
    }
}
@media only screen and (max-width: 568px){
    .Ovrlycrd{
        width:70%;
    }
    .menu{
        display: block;
    }
    .sidemnu{
        display: block;
        position: fixed;
        width: 50%;
        right: -378px;
        top: 0;
        background-color: #771c5a;
        height: 100%;
        color: white;
        box-sizing: border-box;
        padding: 2rem;
        transition: .3s;
    }
    .lnkprt{
        flex-direction: column;
        color: white;
        gap: 1rem;
        width: 100%;
    }
    .link{
        color: white;
    }
    .link:hover{
        text-decoration: underline;
    }
    .lnkp1, .lnkbtn{
        display: none;
    }
    .btnprt{
        width: 80%;
        height: 8%;
        font-size: .7rem;
        margin: 3rem 0;
    }
    .clsdiv{
        width: 100%;   
        display: flex;
        justify-content: flex-end;
    }
    .cls{
        color: white;
        width: 10%;
    }
    .close{
        width: 9%;
    }
}
@media only screen and (max-width: 468px){
    .logo{
        width: 30%;
    }
}

@media only screen and (max-width: 376px){
    .logo{
        width: 40%;
    }
    .menu{
        width: 6%;
    }
    .btnprt{
        width: 100%;
        height: 7%;
        font-size: .7rem;
        margin: 3rem 0;
    }
    .link{
        font-size: .8rem;
    }
    .obtntxt{
        font-size: .9rem;
    }
    .ovrlyhd{
        font-size: 1.1rem;
    }
    .cntnthdC{
        font-size: .8rem;
    }
    .Ovrlycrd{
        height: 15rem;
    }
    .close{
        width: 11%;
    }
}
