.Homepage{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}
.prt1, .prt2, .prt4, .prt5{
    display: flex;
    flex-direction: row;
    font-family: 'Red Rose', cursive;
    justify-content: space-between;
    box-sizing: border-box;
    width: 100%;
}
.prt1{
    padding: 7.5rem 7% 4rem 7%;
}
.lftprt{
    width: 50%;
    padding-top: 2rem;
}
.lfthd{
    font-size: 3.5rem;
}
.diftxt{
    background-image: linear-gradient(90deg, #A02279 11.45%, #A02279 11.45%);
    background-clip: text;
    color: transparent;
    -webkit-text-stroke: 0;
    font-weight: 800;
}
.lfttxt{
    font-size: 1.4rem;
    padding: 2.5rem 0;
    width: 94%;
    line-height: 2rem;
}
.lftfrm{
    width: 95%;
    height: 3.3rem;
    padding: .5rem 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}
label{
    height: 100%;
    width: 65%;
}
#srch{
    width: 100%;
    height: 100%;
    border-radius: 8px 0px 0px 8px;
    border: 1px solid #A3A3A3;
    padding: 0 1rem;
}
#srch:focus{
    border: 1px solid black;
    outline: none;
}
::placeholder {
    color: #B8B8B8;
    font-size: .9rem;
  }
#search{
    width: 35%;
    height: 100%;
    border-radius: 0px 8px 8px 0px;
    background-image: linear-gradient(90deg, #A02279 11.45%, #A02279 11.46%);
    border: none;
    color: white;
    font-size: 1rem;
    cursor: pointer;
}
#search:hover{
    background-image: linear-gradient(90deg, #771c5a 11.45%, #771c5a 11.46%);
}
input{
    box-sizing: border-box;
    font-family: 'Red Rose', cursive;
}
.rgtprt{
    width: 40%;
    display: grid;
    gap: .5rem;
}
.img2, .img1, .img3,.img4{
    width: 100%;
    height: 100%;
}
.img2{
    grid-column: 1 / 2;
    grid-row: 2 / 4;
}
.img3{
    grid-column: 2 / 3;
    grid-row: 3 / 5;
}
.img4{
    grid-column: 1 / 2;
    grid-row: 4 / 6;
}
.img1{
    grid-column: 2 / 3;
    grid-row: 1 / 3;
    width: 100%;
}
.prt2{
    background-image: linear-gradient(90deg, #A02279 11.45%, #A02279 11.45%);
    padding: 0 9%;
    height: 4.3rem;
    align-items: center;
}
.prt22, .prt21, .prt23{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    width: 19%;
}
.prt22{
    font-family: 'Oxygen Mono', monospace;
    font-size: 1.8rem;
    letter-spacing: 3px;
    gap: 10px;
}
.prt21{
    font-size: 2.5rem;
    font-weight: 650;
    font-family: 'Poppins', sans-serif;
}
.prt23{
    font-size: 2rem;
    font-weight: 650;
    gap: 10px;
    font-family: 'Poppins', sans-serif;
}
.prt2 span img{
    width: 20%;
}
.prt3{
    width: 100%;
    padding: 0 7% 4rem 7%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    font-family: 'Red Rose', cursive;
}
.crdtxt{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 5rem;
    padding-top: 1rem;
    box-sizing: border-box;
}
.lftcrdtxt, .rgtcrdtxt{
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: .7rem;
}
.rgtcrdtxt{
    align-items: flex-end;
}
.card{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    box-sizing: border-box;
    border: 1px solid #D7D7D7;
    border-radius: 15px;
    font-size: .75rem;
}
.crdimg1{
    width: 100%;
}
.carddiv{
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto;
    gap: 1.5rem;
}
.prt3hd, .prt4hd{
    font-size: 3rem;
    font-weight: 700;
}
.prt3hd{
    text-align: center;
    margin: 4rem 0;
}
.pr{
    font-weight: 600;
}
.prt4{
    background-image: linear-gradient(90deg, #A02279 11.45%, #A02279 11.45%);
    border: 1px solid #000000;
    color: white;
    padding: 6rem 7%;
}
.prt4hd{
    margin: 4rem 0 2.5rem 0;
}
.prt4txt{
    font-size: 1.1rem;
    font-weight: 400;
    line-height: 2.5rem;
    padding-bottom: 4.5rem;
}
.grppic{
    width: 60%;
}
.btnlrn{
    border-radius: 8px;
    background-color: white;
    font-size: 1rem;
    color: #A02279;
    width: 10%;
    height: 1rem;
    padding: 1rem 2.5rem;
    cursor: pointer;
    transition: .05s;
}
.btnlrn:hover{
    background-color: #A02279;
    color: white;
    border:  2px solid white;
}
.prt5{
    background-color:  #1D1D1E;
    padding: 2.5rem 8%;
    color: white;
}
.lgotxt{
    font-size: 2.5rem;
}
.logodets{
    display: flex;
    flex-direction: row;
    align-items:  baseline;
}
.lgo{
    width: 15%;
}
.sbprt5{
    display: flex;
    flex-direction: column;
    width: 20%;
    height: 10rem;
    gap: .7rem;
}
.frstft{
    justify-content: space-between;
}
.socials, .copyright{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.social{
    cursor: pointer;
}
.socials{
    width: 60%;
    justify-content: space-between;
}
.copyright{
    gap: .6rem;
}
.sbprt5hd{
    font-weight: bold;
    font-size: 1.1rem;
    padding-top: 1rem;
}
.sblst{
    font-size: .8rem;
    font-weight: 400;
}


/* media queries */
@media only screen and (max-width: 1173px){
    .rgtprt{
        width: 45%;
    }
    .lfthd{
        font-size: 3rem;
    }
    .grppic{
        width: 50%;
    }
    .lftprt{
        width: 40%;
        padding-top: 2rem;
    }
    .lgotxt{
        font-size: 2.2rem;
    }
    .lgo{
        width: 15%;
    }
    .rgtcrdtxt{
        display: none;
    }
}

@media only screen and (max-width: 1073px){
    .lftprt{
        width: 50%;
    }
    .prt1{
        padding: 6.5rem 6% 4rem 6%;
    }
    .prt2{
        padding: 0 8%;
        height: 4.2rem;
    }
    .prt3, .prt4, .prt5{
        padding-left: 6%;
        padding-right: 6%;
    }
    .prt22{
        font-size: 1.3rem;
    }
    .prt21{
        font-size: 2rem;
    }
    .prt23{
        font-size: 1.5rem;
    }
    .prt3hd, .prt4hd{
        font-size: 2.5rem;
    }
    .lgotxt{
        font-size: 2.1rem;
    }
}
@media only screen and (max-width: 943px){
    .lfttxt{
        font-size: 1.3rem;
        width: 100%;
        line-height: 2rem;
    }
    .prt21{
        width: 23%;
    }
    .lftfrm{
        width: 100%;
        height: 3rem;
    }
    .rgtprt{
        width: 43%;
        height: 22rem;
    }
    .prt1{
        align-items: center;
        padding: 5rem 6% 4rem 6%;
    }
    ::placeholder {
        font-size: .8rem;
      }
      #search{
        font-size: .9rem;
    }
    .lgo{
        width: 18%;
    }
    .sbprt5hd{
        padding-top: .8rem;
    }
}
@media only screen and (max-width: 843px){
    .lfthd{
        font-size: 2.5rem;
    }
    .prt22{
        font-size: 1.2rem;
    }
    .prt21{
        font-size: 1.6rem;
    }
    .prt23{
        font-size: 1.3rem;
    }
    .prt3hd, .prt4hd{
        font-size: 2.2rem;
    }
    .cpytxt{
        font-size: .9rem;
    }
    .sbprt5hd{
        padding-top: .6rem;
    }
    .cpy{
        width: 10%;
    }
    .lgotxt{
        font-size: 1.8rem;
    }
}
@media only screen and (max-width: 768px){
    .prt1, .prt4, .prt5{
        display: flex;
        flex-direction: column;
        font-family: 'Red Rose', cursive;
        justify-content: space-between;
        box-sizing: border-box;
        width: 100%;
    }
    .lftprt{
        width: 90%;
        padding-top: 0;
    }
    .rgtprt{
        width: 90%;
        padding-top: 2rem;
    }
    .prt1{
        padding: 4rem 6% 4rem 6%;
        height: 68rem;
        justify-content: flex-start;
    }
    .carddiv{
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
    }
    .prt4{
        padding: 4rem 7%;
        align-items: center;
    }
    .prt4hd{
        margin: 0 0 2.5rem 0;
    }
    .grppic{
       display: none;
    }
    .prt5{
        display: grid;
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
        justify-content: stretch;
        gap: 1rem;
    }
    .sbprt5{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 10rem;
        gap: .7rem;
    }
    .social{
        cursor: pointer;
    }
    .socials{
        width: 20%;
    }
    .lgo{
        width: 8%;
    }
    .cpy{
        width: 5%;
    }
    .frstft{
        justify-content:space-evenly;
    }
    .logodets{
        align-items: baseline;
        gap: .3rem;
    }
}
@media only screen and (max-width: 668px){
    .lfttxt{
        font-size: 1.1rem;
        width: 100%;
        line-height: 2rem;
        padding: 1.5rem 0;
    }
    .prt1{
        padding: 4rem 6% 4rem 6%;
        height: 60rem;
        justify-content: flex-start;
    }
    .prt3hd{
        margin: 3rem 0;
    }
}

@media only screen and (max-width: 568px){
    .menu{
        display: block;
    }
    .prt1{
        padding: 2rem 6% 4rem 6%;
        height: 60rem;
        justify-content: flex-start;
    }
    .socials{
        width: 30%;
    }
    .cpy{
        width: 7%;
    }
    .prt22{
        font-size: 1.1rem;
    }
    .prt21{
        font-size: 1.3rem;
    }
    .prt23{
        font-size: 1.2rem;
    }
    .prt2{
        justify-content: flex-start;
        gap: 5rem;
        height: 3.9rem;
    }
}

@media only screen and (max-width: 468px){
    .prt1{
        height: 55rem;
    }
    .prt22{
        font-size: 1.1rem;
    }
    .prt21{
        font-size: 1.1rem;
    }
    .prt23{
        font-size: 1.1rem;
        margin-left: 2.5rem;
    }
    .prt2{
        gap: 3rem;
    }
}

@media only screen and (max-width: 376px){
    .prt1{
        height: 50rem;
    }
    .cpy{
        width: 9%;
    }
    .carddiv{
        grid-template-columns: auto ;
        grid-template-rows: auto auto;
        width: 90%;
    }
    .prt3{
        align-items: center;
    }
    .lfthd{
        font-size: 1.8rem;
    }
    .lfttxt{
        font-size: 1rem;
        width: 100%;
        line-height: 2rem;
        padding: 1.5rem 0;
    }
    .prt3hd, .prt4hd{
        font-size: 1.7rem;
    }
    .prt4txt{
        font-size: 1rem;
    }
    .lgotxt{
        font-size: 1.6rem;
    }
    .sbprt5hd{
        font-size: .9rem;
        padding-top: 0;
    }
    .lgo{
        width: 11%;
    }
    .socials{
        height: .5rem;
        width: 35%;
    }
    .btnlrn{
        font-size: .9rem;
        padding: 1rem 1.8rem;
    }
    .prt4txt{
        padding-bottom: 3rem;
        line-height: 1.8rem;
    }
    .frstft{
        justify-content: flex-start;
        gap: 2rem;
    }
    .prt22{
        font-size: 1rem;
    }
    .prt21{
        font-size: 1rem;
    }
    .prt23{
        font-size: 1rem;
        margin-left: 2.5rem;
    }
    .prt2{
        gap: 2rem;
        padding: 0 6%;
    }
    .sbimg1, .sbimg2, .sbimg3{
        display: none;
    }
    .prt4hd{
        margin: 0 0 1.5rem 0;
    }
}
    